<template>
  <div class="loginBox">
    <div class="title">
      <span class="iconfont icon-left leftIcon"></span>
      <p class="centerText">会员登录</p>
    </div>
    <div class="fromSty">
      <div class="userNameSty">
        <span>用户名:</span>
        <input class="inputSty" v-model="userName" placeholder="请输入邮箱/手机号" type="text" />
      </div>
      <div class="passwordSty">
        <span>密码:</span>
        <input class="inputSty" v-model="password" placeholder="密码" type="password" />
      </div>
      <div>
        <van-button type="danger" block style="border-radius: 5px;" @click="Login()">登录</van-button>
      </div>
    </div>
    <div class="loginAuto">
      <div class="checkbox">
        <van-checkbox v-model="checked" shape="square">自动登录</van-checkbox>
      </div>
      <div class="rightLink">
        <span>
          <router-link to="/register" style="color:rgba(123, 167, 199)">免费注册</router-link>
        </span>
        <span style="padding-left:15px">
          <router-link to="/retrieve_password" style="color:rgba(123, 167, 199)">忘记密码?</router-link>
        </span>
      </div>
    </div>
    <div class="bottomlink">
      <p>第三方登录</p>
    </div>
  </div>
</template>
<script>
export default {
  data: () => ({
    checked: false,
    userName: null,
    password: null
  }),
  created() {},
  methods: {
    Login() {
      //console.log("Login");
      this.$axios
        .login(
          `ym/oauth/token?username=${this.userName}&password=${this.password}&grant_type=password&client_id=app&client_secret=app`
        )
        .then(msg => {
          console.log(msg)
          if (msg.status === 200 && msg.data.code === 0) {
            let userMobileOrPwd = {
              userName: this.userName,
              password: this.password,
              time: +new Date() + 3600 * 1000 * 24 * 7
            };
            if (this.checked) {
              localStorage.setItem("userPwd", JSON.stringify(userMobileOrPwd));
            } else {
              if (JSON.parse(localStorage.getItem("userPwd"))) {
                localStorage.setItem("userPwd", null);
              } else {
                console.log('本地没有缓存')
              }
              console.log("no store pwd");
            }
            this.$store.commit("SAVEUSERINFO", JSON.stringify(msg.data.data));
            this.$Notify({ type: "success", message: msg.data.msg });
            this.$router.push("/");
            this.getUserInfo();
          } else {
            this.$Notify({ type: "danger", message: msg.data.msg });
          }
        });
    },
    getUserInfo() {
      this.$axios.get(`ym/memberuser/user-info`).then(msg => {
        window.console.log(msg);
        if (msg.code === 0) {
          this.$store.commit("SAVEMEMBERINFO", JSON.stringify(msg.data));
        } else {
          //console.log('获取用户信息失败！')
        }
      });
      this.$axios.get(`ym/menu/list`).then(msg => {
        window.console.log(msg);
      });
    }
  }
};
</script>
